import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import { recordStockIn, recordStockAdjustment } from "@/services/api";
import { Button } from "@/components/ui/button";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { CalendarIcon } from "lucide-react";
import { cn } from "@/lib/utils";
import { format } from "date-fns";
import { useToast } from "@/hooks/use-toast";
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card";
import { Product } from "@/types";
import { SearchableSelect } from "@/components/ui/SearchableSelect";

const formSchema = z.object({
  productId: z.string().min(1, ""),
  operationType: z.enum(["stock-in", "adjustment"]),
  quantity: z.coerce.number().int(),
  reason: z.string().optional(),
  notes: z.string().optional(),
  batchExpiryDate: z.date().optional(),
});

type FormValues = z.infer<typeof formSchema>;

interface InventoryOperationFormProps {
  products: Product[];
}

export function InventoryOperationForm({ products }: InventoryOperationFormProps) {
  const { toast } = useToast();
  const queryClient = useQueryClient();

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      productId: "",
      operationType: "stock-in",
      quantity: 0,
      reason: "",
      notes: "",
    },
  });

  const operationType = form.watch("operationType");

  const mutation = useMutation({
    mutationFn: (data: FormValues) => {
      if (data.operationType === "stock-in") {
        return recordStockIn({
          productId: data.productId,
          quantity: data.quantity,
          notes: data.notes,
          batchExpiryDate: data.batchExpiryDate,
        });
      } else {
        return recordStockAdjustment({
          productId: data.productId,
          quantityChange: data.quantity,
          reason: data.reason,
          notes: data.notes,
        });
      }
    },
    onSuccess: () => {
      toast({ title: "", description: "" });
      queryClient.invalidateQueries({ queryKey: ["inventoryMovements"] });
      queryClient.invalidateQueries({ queryKey: ["products"] });
      form.reset();
    },
    onError: (error: any) => {
      toast({
        variant: "destructive",
        title: "",
        description: error.response?.data?.error || "",
      });
    },
  });

  const onSubmit = (data: FormValues) => {
    mutation.mutate(data);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle></CardTitle>
        <CardDescription></CardDescription>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid md:grid-cols-2 gap-6">
              <FormField
                control={form.control}
                name="productId"
                render={({ field }) => (
                  <FormItem className="flex flex-col">
                    <FormLabel></FormLabel>
                     <SearchableSelect
                        placeholder=""
                        searchPlaceholder="..."
                        emptyMessage=""
                        items={products.map(p => ({ value: p._id, label: p.name }))}
                        value={field.value}
                        onChange={field.onChange}
                      />
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="operationType"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel></FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl>
                        <SelectTrigger><SelectValue placeholder="" /></SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        <SelectItem value="stock-in"></SelectItem>
                        <SelectItem value="adjustment"></SelectItem>
                      </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="quantity"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>
                      {operationType === 'stock-in' ? '' : ''}
                    </FormLabel>
                    <FormControl>
                      <Input type="number" placeholder="100  -20" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              {operationType === 'stock-in' ? (
                <FormField
                  control={form.control}
                  name="batchExpiryDate"
                  render={({ field }) => (
                    <FormItem className="flex flex-col pt-2">
                       <FormLabel> ()</FormLabel>
                      <Popover>
                        <PopoverTrigger asChild>
                          <FormControl>
                            <Button
                              variant={"outline"}
                              className={cn("w-full pl-3 text-left font-normal", !field.value && "text-muted-foreground")}
                            >
                              {field.value ? format(field.value, "PPP") : <span></span>}
                              <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                            </Button>
                          </FormControl>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                          <Calendar mode="single" selected={field.value} onSelect={field.onChange} />
                        </PopoverContent>
                      </Popover>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              ) : (
                <FormField
                  control={form.control}
                  name="reason"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel></FormLabel>
                      <Select onValueChange={field.onChange} value={field.value}>
                        <FormControl>
                          <SelectTrigger><SelectValue placeholder="" /></SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          <SelectItem value="damaged"></SelectItem>
                          <SelectItem value="stock_correction"></SelectItem>
                          <SelectItem value="theft"></SelectItem>
                          <SelectItem value="other"></SelectItem>
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}
              <FormField
                control={form.control}
                name="notes"
                render={({ field }) => (
                  <FormItem className="md:col-span-2">
                    <FormLabel> ()</FormLabel>
                    <FormControl>
                      <Textarea placeholder="..." {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
            <Button type="submit" disabled={mutation.isPending}>
              {mutation.isPending ? "..." : ""}
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
} 